*,
*:before,
*:after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-family: $f-cabin;
  font-size: 100%;
  line-height: 1.5;
}

body {
  color: $c-black;
  position: relative;
  overflow-x: hidden;
}



